*{
  margin: 0;
  padding: 0;
}
.App{
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
}
.navigation{
  height: 100vh;
  width: 18vw;
  background-color: black;
  color:white;
}
.nav_upper{
  height:35vh;
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav_lower{
  height: 65vh;
  width: 100%;
}
.nav_links{
  border-top: 1px solid white;
  padding-top:2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  height: 70%;
  width: 100%;
}
.nav_link{
  font: Hahmlet;
  font-size: 1.4rem;
  color: white;
  text-decoration: none;
}
.nav_link:hover{
  background-color: #28282B;
  height: 6vh;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
.content{
  height: 100vh;
  width: 82vw;
}
.App{
  display: flex;
}
.blooddrop{
  height:90%;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.blooddrop_img{
  height: 100%;
  width: 70%;
  color: transparent;
}
*{
    margin: 0;
    padding: 0;
}
.home{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;}
.home_container{
    height: 80vh;
    width: 70%;
    display: flex;
    justify-content:space-evenly;
    align-items:center;
}
.hc_left{
    height:100%;
    flex:0.7 1;
    display:flex;
    align-items:center;
    justify-content: space-evenly;
    flex-direction: column;

}
.hc_right{
    height: 100%;
    flex:0.3 1;
    display:flex;
    align-items:center;
    justify-content: center;
    
}
.hc_left>h1{
    color:white;
    font-family: 'Lato';
    font-size: 10vh;
}
.hc_left>p{
    color: #EEEEEE;
    line-height: 2rem;
    font-size: 1.4rem;
}
.home_video{
    position: absolute;
    z-index: -2;
    object-fit:cover;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
*{
    margin: 0;
    padding: 0;
    box-sizing:border-box;
}
.bloodbank{
    height: 100vh;
    width: 100%;
    padding: 2rem;
    flex-wrap: wrap; 
    background-image: linear-gradient(45deg,#88FFF7,#7C83FD);
}
.bloodtype_container{
    height:100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
.blood_f{
    height: 30vh;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
.blood_s{
    height: 30vh;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
.bloodtype{
    height: 250px;
    width: 250px;
    background-color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 50px;
    font-family: 'Lato', sans-serif;
    box-shadow: 5px 5px #888888;
    border:1px solid #888888;
    flex-wrap: wrap;
    color: red;
    font-size: 2rem;
    font-family: 'Lato';
}
.special{
    color: red;
}
.bloodtype:hover{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    transition: all 0.5s;
}
.bloodtype:active{
    -webkit-transform: translateY(7px);
            transform: translateY(7px);
    transition: all 0.5s;
}
*{
    margin:0;
    padding: 0;
}
.Adminlogin{
    height:100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(45deg,#D83A56,#FF616D);
}
.adminlogin_container{
    height: 50vh;
    width: 30vw;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-evely;
    align-items: center;
    border-radius:10px;
}
.adminlogin_heading{
    height: 30%;
    width: 100%;
    font-family: 'Lato';
    font-size: 1.4rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.adminlogin_details{
    height: 100%;
    width: 100%;
}
.adminlogin_details>form{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.input_field{
    height:7vh;
    width: 90%;
    border: none;
    outline: none;
    padding-left: 20px;
    border-bottom: 1px solid black;
}
.btn{
    font-family: 'Lato';
    font-size: 1.2rem;
    border:none;
    outline: none;
    color: white;
    height: 7vh;
    border-radius:10px;
    width: 10vw;
    margin-top: 1rem;
    background-image: linear-gradient(to right,#A03C78,#ED8E7C);
}
.btn:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    transition: all 0.5s;
}
.btn:active{
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
    transition: all 0.5s;
}
.adminreg_location{
    width: 90%;
    height: 7vh;
    margin-top:7px;
    outline: none;
    border: none;
    border-bottom: 1px solid black;
    padding-left: 20px;
    color: grey;
}
.regsp{
    height: 80vh !important;
}
*{
    margin: 0;
    padding: 0;
}
.Hospital_details{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items:center;
    background-image: linear-gradient(to right,#19376d,#6399eb);
}
.hospital_details_container{
    height:90%;
    width: 50%;
    background:white;
    border-radius: 10px;
    
}
.hospital_details_container>form{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.hospital_details_bloodtype{
    height: 6vh;
    width: 50%;
    background-color: red;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.hospital_details_bloodtype>label{
    font-size:1.3rem;
    font-family: 'Lato';
    color: white;
}
.o{
    background-color: #64C9CF;
}
.AB{
    background-color: #F8485E;
}
.A{
    background-color:#FF4C29;
}
.B{
    background-color:#54436B;
}
.hospital_details_heading{
    color: white;
    font-family: 'Lato';
    font-size:2.5rem;
}
.hospital_details_bloodtype>input{
    outline: none;
    border: none;
    height: 70%;
    border-radius: 10px;
    padding-left: 3rem;
}
.subbtn{
    height: 5vh;
    width: 50%;
    outline: none;
    border: none;
    font-size: 1.5rem;
    background-image: linear-gradient(to right,#7C83FD,#96BAFF);
    color: white;
    border-radius:10px;
    display: flex;
    justify-content: center;
    align-items:center;
}
.subbtn:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    transition: all 0.5s;
}
.subbtn:active{
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
    transition: all 0.5s;
}
*{
    margin: 0;
    padding: 0;
}
.Results{
    display: flex;
    height: 100vh;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.container{
    width: 60%;
    max-height: 100vh;
    overflow-y: scroll;
}
.resulthospital{
    margin:30px;
    height: 20vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid red;
    border-radius: 5px;
    font-family: 'Lato';
    font-size: 1.2rem;
}
.resulthospital>img{
    height: 100%;
width: 30%;
}
.search>form>select{
    width: 20vw;
    height: 10vh;
    outline: none;
    border-radius: 7px;
    text-align: center;
    border: 1px solid darkblue;
    
}
.container::-webkit-scrollbar{
    width: 1px;
}
.search>form>select>option{
    margin-top: 20px;
    font-size: 1rem;
    display: block;
    height: 5vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}
.search>form>select>option:hover{
    background-color: #efefef;
}.results_empty{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.results_empty>h1{
    font-family: 'Lato';
}

